

:root {
    --default: "Roboto" , sans-serif;
    --gray: #828282;
    --blue: #56ccf2;
    --yellow: #f2c94c;
    --white: #fff;
    --black: #000;
    --gold: #c8a35f;
    --dark: #2f343a;
}

*, ::after, ::before {
    box-sizing: border-box;
}

body, html {
    margin: 0;
    padding: 0;
    font-family: var(--default);
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: var(--fonts-gray);
    background-color: var(--white);
}

.container {
    max-width: 1170px;
    margin: 0 auto;

    @media (max-width: 1170px) {
        width: 95%;
    }
}

.wrapper {
    background: url("/img/bg.png");

    &__ball {
        position: absolute;
        width: 769px;
        height: 769px;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        margin: auto;
    }
}

.logo {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 100px;
    line-height: 117px;
    text-align: center;
    border: 1px solid #FFFFFF;
    margin: 0 auto;
    width: 203px;
    height: 203px;
    color: white;
    padding-top: 40px;

    &__disc {
        text-align: center;
        font-size: 35px;
        line-height: 41px;
        color: #FFFFFF;
    }
}

.company {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 495px;
    height: 450px;

    &__info {
        font-family: SeoulNamsan;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        letter-spacing: 0.24em;
        color: #C7C7C7;
        margin-bottom: 30px;
    }

    &__title {
        font-family: SeoulNamsan;
        font-style: normal;
        font-weight: normal;
        font-size: 50px;
        line-height: 40px;
        color: #FFFFFF;
        margin-bottom: 33px;
    }

    &__disc {
        font-family: SeoulNamsan;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 25px;
        color: #FFFFFF;
        width: 474px;
        width: 474px;
    }
}





/***********************************************************/

#fp-nav .active span {
    background: white;
}

#fp-nav ul li a span, .fp-slidesNav ul li a span {
    background: white;
}

#fp-nav ul li, .fp-slidesNav ul li {
    
    
    margin-bottom: 15px;
    
}

/***********************************menu************************/
#myMenu {
    position: absolute;
    z-index: 999;
    list-style-type: none;
    font-size: 16px;
    line-height: 175.69%;
    text-align: right;
    color: #FFFFFF;
    right: 56px;
    top: 362px;
    

    & a {
        color: #808080;
        text-decoration-line: none;
        transition: all .3s;

        &:hover {
            color: white;
        }
    }

    & .active a{
        color: white;
    }
}

